:root {
  --bg-1-color: #3b283e;
  --bg-2-color: #4e0537;
  --app-1-color: #5e4961ad;
  --app-2-color: #c402d1;
  --columns-1-color: #5e4961ad;
  --columns-2-color: #8c0898;
  --task-1-color: #e19fc2;
  --task-2-color: #45044a47;
  --text: #ffc3cd;
  --html-redius: 20px;
}

* {
  border-radius: var(--html-redius);
  font-family: Georgia, emoji;
}

body {
  background-image: linear-gradient(90deg, var(--bg-1-color, #3b1840), var(--bg-2-color));
}

.App {
  text-align: center;
  background: var(--app-1-color);
  color: var(--text);
  border: 14px ridge var(--app-1-color);
  margin: 23px;
  padding: 10px 10px 100px;
}

.scrum-board {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.add-task-form {
  border: 10px inset var(--app-1-color);
  margin-bottom: 20px;
}

.add-task-form input, .add-task-form select, .add-task-form button {
  border-color: var(--app-2-color);
  background: var(--task-1-color);
  margin: 10px;
  padding: 10px;
}

.columns {
  justify-content: space-around;
  width: 100%;
  display: flex;
}

.column {
  border: 10px inset var(--columns-1-color);
  width: 30%;
  padding-bottom: 20px;
}

.task {
  border: 4px inset var(--task-1-color);
  background: var(--task-2-color);
  margin: 10px 5%;
  padding: 10px;
  transition: background-color .3s;
}

.task:hover {
  background-color: #cc1ddc61;
}

.task input {
  text-align: center;
  background: var(--task-1-color);
  margin: 10px;
  padding: 10px;
}

.task button {
  background: var(--task-1-color);
  justify-content: center;
  width: 100px;
  margin: 10px;
  padding: 10px;
}

button, select, input {
  transition: background-color .3s;
}

button:hover, select:hover, input:hover {
  color: #000;
  background-color: #fff;
}
